.menu {
    position: fixed;
    display: none;
    z-index: 5;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: $text-dark;
    overflow: hidden;
    @include vertical;
    @include sm {
        overflow: scroll;
        height: 100vh;
        padding-top: $main-margin-top-sm;
        padding-bottom: 0; }
    &__container {
        height: 100%; }
    &__item {
        font-size: 50px;
        line-height: 1;
        margin-bottom: 1vh;
        color: $main-menu-color;
        @include css-lock(35,50,767,1400);
        transition: color 0.7s 0.2s;
        @include sm {
            font-size: 35px; }
        &.is-active {
            a {
                color: $orange;
                border-bottom: 2px solid $orange; } } }
    &__motto {
        max-width: 320px;
        font-size: 17px;
        line-height: 23px;
        color: $main-menu-color-muted;
        @include css-lock(12,20,767,1400); }
    .label {
        color: $main-menu-color-muted;
        @include sm {
            font-size: 17px; } }
    .feature-list {
        @include sm {
            margin-bottom: 30px; } }
    .email,
    .adress,
    .phone {
        color: $main-menu-color;
        transition: color 0.7s 0.2s;
        @include css-lock(12,20,767,1400); } }

.hovered.menu {
    .lang {
        &__item.is-active {
            color: $main-menu-color-muted;
            transition: color 0.7s 0.2s; } }
    .menu__item,
    .social__item,
    .time,
    .email,
    .menu-bottom .email,
    .adress,
    .phone {
        color: $main-menu-color-muted;
        transition: color 0.7s 0.2s; } }

.light {
    color: #fff !important;
    transition: color 0.7s 0.2s;
    .menu__item,
    .social__item,
    .time,
    .email,
    .menu-bottom .email,
    .adress,
    .phone {
        color: #fff !important;
        transition: color 0.7s 0.2s; } }



// ===== MAIN MENU ROWS =====

.header .menu {
    .container-inner {
        padding: 0 36px; } }

.menu .wrapper {
    min-height: 700px;
    vertical-align: middle;
    display: inline-block; }


.margin-top-3vh {
    margin-top: 3vh; }

.margin-top-6vh {
    margin-top: 6vh; }

// ===== TOP / BOTTOM MENUS =====

.menu-top {
    position: relative;
    z-index: 10;
    // transition: background-color 0.s
    // transition-delay: all 1s
    @include sm {
        .header.fixed & {
            background-color: $text-dark;
            transition: background-color 0.1s 0.5s;
            padding-bottom: 16px;
 } } }            // transition-delay: all 1s

.menu-top-container {
    align-items: center;
    &>* {
        display: flex;
        align-items: center; } }

.nav {
    &-item:first-child {
        margin-right: 38px;
        @include sm {
            margin-right: 20px; } }
    &-item--wider {
        margin-right: 54px !important;
        @include md {
            margin-right: 30px !important; } }
    &-item {
        display: inline-block;
        position: relative;
        overflow: hidden; }
    &-item:hover .arrow {
        visibility: visible;
        opacity: 1; } }

.menu-top {
    .nav-item {
        color: $text-dark;
        &.is-active {
            color: $orange; }
        .dark-theme & {
            color: $main-menu-color; }
        .dark-theme &.is-active {
            color: $main-menu-color-muted; }
        &:after {
            position: absolute;
            top: 90%;
            left: 0;
            right: 0;
            width: 100%;
            height: 1px;
            background-color: $text-dark;
            content: '';
            transform: translateX(-100%);
            transition: transform 0.2s; }
        &:hover:after {
            transform: translateX(0);
            transition: transform 0.2s; }
        &.is-active:after {
            background-color: $orange; }
        .dark-theme &:after {
            background-color: $main-menu-color; }
        .dark-theme &.is-active:after {
            background-color: $main-menu-color-muted; } } }

.logo {
    display: flex;
    max-width: 88px;
    max-height: 30px;
    position: relative;
    z-index: 10000;
    svg {
        width: 88px; }
    &.logo-grey {
        svg {
            fill: $main-menu-color;
            display: inline-block; } }
    .dark-theme & {
        svg {
            fill: $main-menu-color; } }
    @include sm {
        padding-top: $header-top-padding-sm;
        svg {
            height: 20px;
            width: 75px; } } }

.menu {
    .menu-bottom {
        position: absolute;
        z-index: 3000;
        bottom: 0;
        padding-bottom: 30px;
        left: 0;
        right: 0;
        font-size: 20px;
        text-align: left;
        @include css-lock(12,20,767,1400);
        .email {
            text-decoration: underline;
            color: $light-grey;
            transition: color 0.7s 0.2s; }
        @include sm {
            position: relative;
            padding-bottom: 10px;
            padding-top: 105px; } }

    .lang {
        font-size: 20px;
        color: $main-menu-color-muted;
        transition: color 0.7s 0.2s;
        @include css-lock(12,20,767,1400);
        &__item.is-active {
            color: $main-menu-color;
            transition: color 0.7s 0.2s; }
        &--mob {
            padding-bottom: 64px;
            padding-top: 10px; } } }

.time {
    font-size: 17px;
    color: $light-grey;
    transition: color 0.7s 0.2s; }

.margin-110 {
    margin-bottom: 10vh; }

.margin-64 {
    margin-bottom: 64px; }

.jobs {
    margin-bottom: 54px;
    @include css-lock(12,20,767,1400);
    color: $main-menu-color; }

.social {
    &__item {
        display: inline-block;
        color: $light-grey;
        transition: color 0.7s 0.2s;
        &:hover .arrow {
            visibility: visible;
            opacity: 1; }
        @include sm {
            margin-right: 20px; } }
    &__item:first-child {
        margin-right: 38px; } }


// ===== MENU-TRIGGER =====

.menu-trigger {
    position: absolute;
    top: -8px;
    right: 24px;
    cursor: pointer;
    z-index: 10000;
    padding: 20px;
    @include sm {
        margin-right: -$header-top-padding-sm;
        padding: $header-top-padding-sm;
        top: 0; }
    .menu-circle {
        display: inline-block;
        height: 6px;
        width: 6px;
        border-radius: 50%;
        background-color: #000;
        position: relative;
        z-index: 10000;
        color: #fff;
        transition: transform 0.2s ease-out;
        .dark-theme & {
            background-color: $light-grey; }
        &:nth-child(2) {
            margin: 0 6px; } }
    &-text {
        font-size: 20px; }
    &.menu-is-open {
        .menu-circle {
            background-color: #fff; }
        .menu-circle:nth-child(1) {
            transform: translateX(10px); }
        .menu-circle:nth-child(2) {
            transform: scale(2); }
        .menu-circle:nth-child(3) {
            transform: translateX(-10px); }
        .text-close {
            display: none; }
        .text-open {
            display: block; } } }

.dark-theme {
    .text-close {
        color: $main-menu-color; } }


.text-open {
    display: none;
    color: $main-menu-color; }

.menu-trigger:hover {
    .menu-circle:nth-child(1) {
        transform: translateX(10px); }
    .menu-circle:nth-child(2) {
        transform: scale(2); }
    .menu-circle:nth-child(3) {
        transform: translateX(-10px); } }
