.filter {
    margin: 168px auto 136px;
    @include css-lock(14,20,767,1400);
    @include sm {
        margin-top: $main-margin-top-sm;
        margin-bottom: 0; }
    &__main {
        position: relative;
        padding-top: 60px;
        @include sm {
            padding-top: 0; } }
    &__layout {
        text-align: center;
        margin: 0 -80px;
        transition: opacity 1s, visibility;
        height: auto !important;
        @include sm {
            height: auto !important;
            padding-left: 0;
            margin-left: -15px;
            margin-right: -15px; } }
    &__label {
        font-size: 17px;
        color: $light-grey;
        text-align: left;
        &--dark {
            color: $main-menu-color-muted; } }
    &__buttons {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-flow: column;
        z-index: 5;
        &--main {
            user-select: none;
            @include sm {
                position: static;
                margin-bottom: 52px; } }
        &--filter {
            margin-top: 168px;
            left: 36px;
            @include sm {
                left: 15px;
                margin-top: $main-margin-top-sm; } } }
    &__btn {
        font-size: 20px;
        color: $light-grey;
        text-align: left;
        line-height: 1.4;
        &.is-active {
            color: $main-menu-color-muted; }
        &--selected {
            color: $main-menu-color-muted; }
        &[data-filter="*"] {
            text-decoration: underline; } }
    &__item {
        display: inline-block;
        padding: 0 80px 60px;
        position: static !important;
        top: auto !important;
        left: auto !important;
        @include sm {
            padding: 0 15px 10px;
            max-width: 100%; } }

    &__image {
        @include cover;
        padding-bottom: 247px;
        position: relative;
        z-index: 3;
        transition: 2.5s transform;
        &:hover {
            transform: translateY(42px);
            transition: 0.7s transform;
            @include sm {
                transform: translatey(0); } }
        &--small {
            width: 164px;
            @include sm {
                padding-bottom: 120%; } }
        &--middle {
            width: 350px;
            @include sm {
                max-width: 100%;
                padding-bottom: 72%; } }
        &--big {
            width: 439px;
            @include sm {
                max-width: 100%;
                padding-bottom: 58%; } } }
    &__text {
        position: relative;
        top: -247px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        @include sm {
            display: none; } }

    &__next {
        text-align: center;
        position: relative;
        .icon {
            fill: $orange;
            font-size: 0.75em; }
        @include sm;
        margin-top: 54px;
        margin-bottom: 64px; }



    // ===== BUTTONS POPUP =====

    &__filter {
        opacity: 0;
        visibility: hidden;
        transition: opacity 1s, visibility 1s;
        &.is-active {
            opacity: 1;
            visibility: visible;
            position: fixed;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            z-index: 10000;
            background-color: $text-dark;
            overflow: hidden;
            transition: opacity 1s, visibility 1s; } } }




