.header {
    position: absolute;
    top: 0;
    left: 0;
    right: 8px;
    padding-top: 36px;
    z-index: 1000;
    background-color: #fff;
    .container-inner {
        padding: 0 28px 0 36px;
        @include sm {
            padding: 0 7px 0 15px; } }
    .container-inner {
        position: relative;
        z-index: 500; }
    .dark-theme & {
        background-color: transparent; }
    &.fixed {
        position: fixed; }
    @include sm {
        padding-top: 0; } }














