.footer {
    padding-bottom: 22px;
    overflow: hidden;
    // width: 100%
    @include gpu;
    background-color: #fff;
    position: absolute;
    z-index: 500;
    bottom: 0;
    left: 0;
    right: 0;
    margin-right: 8px;
    .container-inner {
        padding: 0 28px 0 36px;
        @include sm {
            padding: 0 7px 0 15px; } }
    // margin-right: 7px
    .dark-theme & {
        color: $light-grey;
        background-color: $text-dark; }
    @include sm {
        padding-bottom: 10px; } }

.is-hidden {
    display: none; }

.is-visible {
    display: block; }

.pagination {
    position: absolute;
    top: 0;
    right: 42px;
    z-index: 300;
    font-size: 18px;
    color: $light-grey; }

.nav-email {
    position: relative;
    display: block;
    &:after {
        position: absolute;
        bottom: 100%;
        left: 50%;
        // width: 100%
        opacity: 0;
        font-size: 16px;
        content: attr(data-email);
        transform: translate(-50%, 100px);
        transition: transform 0.2s, opacity 0.9s;
        background-color: rgba(0,0,0,.2);
        border-radius: 5px;
        padding: 10px; } }


.nav-email:hover:after {
        display: block;
        transform: translate(-50%, -10px);
        opacity: 1;
        transition: transform 0.2s, opacity 0.9s; }

.up {
    font-size: 17px;
    color: $light-grey; }
